strong {
  font-weight: 400;
  @include bold; }

small {
  font-size: inherit; }

em, i {
  font-style: normal; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.title {
  @include css-lock(28, 55, 320, 1199, font-size);
  @include bold;
  color: $c-heading;
  line-height: 1;
  .icon {
    vertical-align: middle;
    line-height: 1.2;
    @include css-lock(16, 28, 320, 1199, font-size); }
  &--white {
    color: $white; } }

.subttl {
  @include css-lock(18, 24, 320, 1199, font-size);
  color: $white; }

.h1, h1 {
  @extend .title; }

.h2, h2 {}

.h3, h3 {}

.h4, h4 {
  @include bold;
  color: $c-heading;
  text-transform: uppercase;
  line-height: 1.2;
  @include css-lock(18, 24, 320, 1199, font-size);
  &--ttn {
    text-transform: none; }
  &--white {
    color: $white; } }

.h5, h5 {}

.h6, h6 {}

.text {
  &--white {
    color: $white; } }
