.header {
  &__logo {
    @include flex(flex-start, center);
    padding-top: 10px;
    padding-bottom: 10px; }
  &__top {
    border-bottom: 1px solid  #b5d0ff;
    @include flex(space-between); }
  &__nav {
    flex-grow: 1;
    height: auto;
    text-align: right;
    padding-right: 20px;
    @include flex(flex-end, center); }
  &__btn {
    @include flex(flex-start, center); }
  &__bottom {
    padding-top: 20px; } }

.phones {
  @include flex(space-between, center); }

.phone {
  @include flex(flex-start, center);
  color: $white;
  @include bold;
  @include tr(color .3s);
  @include hover {
    color: $accent; }
  .icon {
    margin-right: 5px; } }
