.slider__prev, .slider__next { position: absolute; background-color: transparent; top: 50%; transform: translate(0, -50%); width: 90px; height: 20px; color: #0f1e4c; }

.slider__prev::before, .slider__next::before { content: ''; width: 100%; position: absolute; top: 50%; transform: translate(0, -50%); left: 0; height: 1px; background-color: currentColor; }

.slider__prev::after, .slider__next::after { content: ''; width: 10px; height: 10px; border-left: 1px solid currentColor; border-bottom: 1px solid currentColor; transform: rotate(-135deg); position: absolute; top: 50%; transform: translate(0, -50%) rotate(-135deg); right: 0; }

.slider__prev { left: calc(0px - 30px - 90px); transform: translate(0, -50%) rotate(180deg); }

