@include font('Calibri', 'Calibri');
@include font('Calibri-Bold', 'Calibri-Bold');
@include font('Calibri-Light', 'Calibri-Light');

@mixin reg {
  font-family: 'Calibri', Arial, sans-serif; }
@mixin bold {
  font-family: 'Calibri-Bold', Arial, sans-serif; }
@mixin light {
  font-family: 'Calibri-Light', Arial, sans-serif; }
